import {
    Input,
    Popover,
    Carousel,
    CarouselItem,
    Table,
    TableColumn,
    Pagination
} from "element-ui"
import VueLazyload from 'vue-lazyload'
const LOAD_IMG = 'https://hlgcdn.oss-cn-hangzhou.aliyuncs.com/hlg-ui/1574069829136/9727212.png'; // 
export default {
    install(Vue) {
        // base 
        const requireBaseComponents = require.context(
            '@/base',
            true,
            /[a-z0-9]+\.(jsx?|vue)$/i,
        )
        /**
         * requireBaseComponents 
         * keys { Function } - 
         */
        requireBaseComponents.keys().forEach(item => {
            const componentConfig = requireBaseComponents(item);
            const name = componentConfig.default.name;
            if (name) {
                Vue.component(name, componentConfig.default || componentConfig);
            }
        });

        Vue.use(Input)
        Vue.use(Popover)
        Vue.use(Carousel)
        Vue.use(CarouselItem)
        Vue.use(Table)
        Vue.use(TableColumn)
        Vue.use(Pagination)

        Vue.use(VueLazyload, {
            loading: LOAD_IMG, //
            error: LOAD_IMG //
        })
    },
}